import { ref, computed } from 'vue'
import { api } from '../services/api'

export function useVault() {
  const vaults = ref([])
  const stages = ref([])
  const settings = ref({})
  const loading = ref(false)
  const error = ref(null)

  const defaultVault = computed(() => 
    vaults.value.find(v => v.isDefault)?.slug || vaults.value[0]?.slug || 'vault'
  )

  const defaultStage = computed(() => 
    settings.value.default_stage || 'prod'
  )

  async function loadVaults() {
    loading.value = true
    error.value = null
    try {
      const response = await api.listVaults()
      vaults.value = response.vaults
    } catch (err) {
      error.value = err.message
      throw err
    } finally {
      loading.value = false
    }
  }

  async function loadStages() {
    loading.value = true
    error.value = null
    try {
      const response = await api.listStages()
      stages.value = response.stages
    } catch (err) {
      error.value = err.message
      throw err
    } finally {
      loading.value = false
    }
  }

  async function loadSettings() {
    loading.value = true
    error.value = null
    try {
      settings.value = await api.getSettings()
    } catch (err) {
      error.value = err.message
      throw err
    } finally {
      loading.value = false
    }
  }

  async function loadAll() {
    loading.value = true
    error.value = null
    try {
      const [vaultsRes, stagesRes, settingsRes] = await Promise.all([
        api.listVaults(),
        api.listStages(),
        api.getSettings()
      ])
      vaults.value = vaultsRes.vaults
      stages.value = stagesRes.stages
      settings.value = settingsRes
    } catch (err) {
      error.value = err.message
      throw err
    } finally {
      loading.value = false
    }
  }

  async function verifyVaults() {
    loading.value = true
    error.value = null
    try {
      return await api.verifyVaults()
    } catch (err) {
      error.value = err.message
      throw err
    } finally {
      loading.value = false
    }
  }

  return {
    vaults,
    stages,
    settings,
    loading,
    error,
    defaultVault,
    defaultStage,
    loadVaults,
    loadStages,
    loadSettings,
    loadAll,
    verifyVaults
  }
}